import React, { useLayoutEffect, useState } from 'react';
import { Grid } from '@mui/material';
import axios from 'axios';

import Cabecalho from '../../components/Cabecalho';
import Menu from '../../components/Menu';
import Rodape from '../../components/Rodape';

const EpFour = () => {

    const [film, setFilm] = useState([])

    useLayoutEffect(() => {

        axios.get("https://swapi.dev/api/films/1")
            .then((results) => setFilm(results.data))
            .catch((error) => console.log(error))

    }, [])

    return (
        <>

            <Grid container style={{ padding: 10 }}>

                <Grid item md={12} xs={12} sm={12}>
                    <Cabecalho />
                </Grid>

                <Grid item md={12} xs={12} sm={12}>
                    <Menu />
                </Grid>

                <Grid item md={2} xs={12} sm={12}></Grid>
                <Grid item md={8} xs={12} sm={12} style={{ textAlign: 'center' }}>


                    <div
                        className='films'>
                        <h1
                            style=
                            {{
                                textAlign: 'center',
                                fontWeight: 'bold',
                                fontSize: 32
                            }}>
                            Star Wars Episdio 4:
                        </h1>
                        <h2
                            style=
                            {{
                                textAlign: 'center',

                            }}>
                            Uma Nova Esperana
                        </h2>

                        <div
                            className='films'
                            style=
                            {{
                                textAlign: 'justify',
                                lineHeight: 1.5
                            }}>
                            <b>Titulo original: </b> {film.title} <br /> <br />
                            <b>Abertura do filme: </b> {film.opening_crawl} <br /> <br />

                            <b>Abertura do filme em Portugus:</b>  um perodo de guerra civil. Partindo de uma base secreta,
                            naves rebeldes atacam e conquistam sua primeira vitria contra o perverso Imprio Galctico.
                            Durante a batalha, espies rebeldes conseguem roubar os planos secretos da arma decisiva do Imprio, a
                            ESTRELA DA MORTE, uma estao espacial blindada com poder suficiente para destruir um planeta inteiro.
                            Perseguida pelos sinistros agentes do Imprio, a princesa Leia, apressa-se em voltar para casa a bordo de sua
                            nave estelar, protegendo os planos roubados que podem salvar seu povo e restaurar a liberdade na
                            galxia.... <br /> <br />

                            <b>Diretor: </b> {film.director} <br /> <br />
                            <b>Produtor: </b> {film.producer} <br /> <br />
                            <b>Data de Lanamento: </b> {film.release_date} <br /> <br />

                            <b>Sinopse: </b> Em Star Wars - Episdio IV, o jovem Luke Skywalker (Mark Hamill) sonha
                            ir para a Academia como seus amigos, mas se v envolvido em uma guerra intergaltica
                            quando seu tio compra dois robs e com eles encontra uma mensagem da princesa Leia Organa
                            (Carrie Fisher) para o Jedi Obi-Wan Kenobi (Alec Guinness) sobre os planos da construo
                            da Estrela da Morte, uma gigantesca estao espacial com capacidade para destruir um planeta.
                            Luke ento se junta aos cavaleiros Jedi e a Han Solo (Harrison Ford), um mercenrio, para
                            tentar destruir esta terrvel ameaa ao lado dos membros da resistncia. <br /> <br />

                        </div>

                    </div>

                </Grid>

                <Grid item md={12} xs={12} sm={12}>
                    <Rodape />
                </Grid>

            </Grid>
        </>
    );
}

export default EpFour;
