import React from 'react';
import { Grid } from '@mui/material';


import Cabecalho from '../../components/Cabecalho';
import Menu from '../../components/Menu';
import Rodape from '../../components/Rodape';

const Sequence = () => {
    return (
        <>
            <Grid container style={{ padding: 10 }}>

                <Grid item md={12} xs={12} sm={12}>
                    <Cabecalho />
                </Grid>

                <Grid item md={12} xs={12} sm={12}>
                    <Menu />
                </Grid>

                <Grid item md={2} xs={12} sm={12}></Grid>

                <Grid item md={8} xs={12} sm={12} style={{ textAlign: 'center' }}>

                    <div
                        className='films'>
                        <h1
                            style=
                            {{
                                textAlign: 'center',
                                fontWeight: 'bold',
                                fontSize: 32
                            }}>
                            Sequncias para assistir aos filmes:
                        </h1>

                        <div
                            className='films'
                            style=
                            {{
                                textAlign: 'justify',
                                lineHeight: 1.5
                            }}>
                            <h2
                                style=
                                {{
                                    textAlign: 'center'
                                }}>
                                Ordem de Lanamento
                            </h2>

                            Esse  o jeito mais popular e segue a ordem de lanamento de cada um dos ttulos. Mas h ressalvas.
                            Quando George Lucas lanou a trilogia prequela, ele acabou fazendo algumas alteraes digitais
                            na trilogia original. <br /><br /> Isso fez com que certos personagens que s foram apresentados
                            nos filmes lanados a partir do final dos anos 90, aparecessem nos primeiros filmes de
                            Star Wars. Ou seja, se voc achar melhor comear por aqui, pode ficar sem entender algumas
                            coisas, mas nada que prejudique a experincia como um todo.<br /><br />

                            - Star Wars Episdio 4: Uma Nova Esperana <br />
                            - Star Wars Episdio 5: O Imprio Contra-Ataca <br />
                            - Star Wars Episdio 6: O Retorno de Jedi <br />
                            - Star Wars Episdio 1: A Ameaa Fantasma <br />
                            - Star Wars Episdio 2: O Ataque dos Clones <br />
                            - Star Wars Episdio 3: A Vingana dos Sith <br />
                            - Star Wars Episdio 7: O Despertar da Fora <br />
                            - Rogue One: Uma Histria Star Wars <br />
                            - Star Wars Episdio 8: Os ltimos Jedi <br />
                            - Han Solo: Uma Histria Star Wars <br />
                            - Star Wars Episdio 9: A Ascenso Skywalker <br /> <br />

                            Depois de conhecer cada um dos filmes pela ordem em que foram lanados,  possvel assist-los de
                            diferentes formas. Vamos apresentar cada uma delas e voc decide qual se adaptar melhor a experincia
                            que deseja ter com Star Wars. Veja as opes abaixo.

                            <h2
                                style=
                                {{
                                    textAlign: 'center',
                                    marginTop: 25
                                }}>
                                Ordem de Cronolgica
                            </h2>

                            Assistir desse jeito pode estragar as maiores surpresas que a trilogia original pode proporcionar
                            ao expectador. No viver essa experincia cinematogrfica  quase um crime, mas se mesmo
                            assim desejar, voil!<br /> <br />

                            - Star Wars Episdio 1: A Ameaa Fantasma <br />
                            - Star Wars Episdio 2: O Ataque dos Clones <br />
                            - Star Wars Episdio 3: A Vingana dos Sith <br />
                            - Han Solo: Uma Histria Star Wars <br />
                            - Rogue One: Uma Histria Star Wars <br />
                            - Star Wars Episdio 4: Uma Nova Esperana <br />
                            - Star Wars Episdio 5: O Imprio Contra-Ataca <br />
                            - Star Wars Episdio 6: O Retorno de Jedi <br />
                            - Star Wars Episdio 7: O Despertar da Fora <br />
                            - Star Wars Episdio 8: Os ltimos Jedi <br />
                            - Star Wars Episdio 9: A Ascenso Skywalker <br /> <br />

                            <h2
                                style=
                                {{
                                    textAlign: 'center',
                                    marginTop: 25
                                }}>
                                Ordem Ernst Rister
                            </h2>

                            Essa ordem foi criada como uma espcie de cronologia perfeita. O caminho por ela no priva o espectador
                            das grandes revelaes dos episdios IV e V, e no causa estranhamento com a
                            figura que aparece no episdio VI. <br /> <br />

                            - Star Wars Episdio 4: Uma Nova Esperana <br />
                            - Star Wars Episdio 5: O Imprio Contra-Ataca <br />
                            - Star Wars Episdio 1: A Ameaa Fantasma <br />
                            - Star Wars Episdio 2: O Ataque dos Clones <br />
                            - Star Wars Episdio 3: A Vingana dos Sith <br />
                            - Star Wars Episdio 6: O Retorno de Jedi <br />
                            - Star Wars Episdio 7: O Despertar da Fora <br />
                            - Star Wars Episdio 8: Os ltimos Jedi <br />
                            - Star Wars Episdio 9: A Ascenso Skywalker <br /> <br />

                            <h2
                                style=
                                {{
                                    textAlign: 'center',
                                    marginTop: 25
                                }}>
                                Ordem Machete
                            </h2>

                            Muitos fs de Star Wars no gostam do episdio I. Eles ignoram esse filme pelo fato de no ter
                            superado as expectativas de mostrar uma base solidificada para o universo criado por George Lucas.
                            Um f chamado Rod Hilton criou essa ordem e muita gente segue at hoje. Ela no considera
                            o episdio I e nem os episdios mais recentes, mas  o jeito mais rpido de conhecer a
                            trajetria de Luke Skywalker.<br /><br />

                            - Star Wars Episdio 4: Uma Nova Esperana <br />
                            - Star Wars Episdio 5: O Imprio Contra-Ataca <br />
                            - Star Wars Episdio 6: O Retorno de Jedi <br />
                            - Star Wars Episdio 2: O Ataque dos Clones <br />
                            - Star Wars Episdio 3: A Vingana dos Sith <br /><br />

                            Mas lembre-se, apesar de todas as crticas e exageros dos fs sobre o episdio I,
                            ele serve como complemento e no deixa de ser importante para a saga. Se voc optar
                            por assistir desse jeito, o ideal  que entre o episdio III e VII,
                            voc d um jeito de assist-lo. Okay? <br /><br />

                            Agora que voc conhece todas as formas de assistir a saga Star Wars,
                            cuidado para <i>no ser seduzido pelo do lado negro da Fora"</i>.<br /><br />
                        </div>
                    </div>
                </Grid>

                <Grid item md={12} xs={12} sm={12}>
                    <Rodape />
                </Grid>

            </Grid>
        </>
    );
}
export default Sequence;
