import React, { useState, useLayoutEffect } from 'react';
import { Grid } from '@mui/material';
import axios from 'axios';

import Cabecalho from '../../components/Cabecalho';
import Menu from '../../components/Menu';
import Rodape from '../../components/Rodape';


const EpOne = () => {

    const [film, setFilm] = useState([])

    useLayoutEffect(() => {

        axios.get("https://swapi.dev/api/films/4")
            .then((results) => setFilm(results.data))
            .catch((error) => console.log(error))

    }, [])

    return (
        <>

            <Grid container style={{ padding: 10 }}>

                <Grid item md={12} xs={12} sm={12}>
                    <Cabecalho />
                </Grid>

                <Grid item md={12} xs={12} sm={12}>
                    <Menu />
                </Grid>

                <Grid item md={2} xs={12} sm={12}></Grid>
                <Grid item md={8} xs={12} sm={12} style={{ textAlign: 'center' }}>


                    <div
                        className='films'>
                        <h1
                            style=
                            {{
                                textAlign: 'center',
                                fontWeight: 'bold',
                                fontSize: 32
                            }}>
                            Star Wars Episdio 1:
                        </h1>
                        <h2
                            style=
                            {{
                                textAlign: 'center',

                            }}>
                            A Ameaa Fantasma
                        </h2>

                        <div
                            className='films'
                            style=
                            {{
                                textAlign: 'justify',
                                lineHeight: 1.5
                            }}>
                            <b>Titulo original: </b> {film.title} <br /> <br />
                            <b>Abertura do filme: </b> {film.opening_crawl} <br /> <br />

                            <b>Abertura do filme em Portugus:</b> A Repblica Galctica est em grande tumulto.
                            Em disputa esto os impostos sobre as rotas comerciais para os sistemas estelares exteriores.
                            Na esperana de resolver o assunto com um bloqueio de implacveis naves de guerra,
                            a gananciosa federao de comrcio interrompe todos os carregamentos para o pequeno planeta Naboo.
                            Enquanto o Congresso da Repblica debate incessantemente esta alarmante cadeia de acontecimentos, o Chanceler
                            Supremo manda secretamente dois Cavaleiros Jedi, guardies da paz e da justiana galxia,
                            resolver o conflito.... <br /> <br />

                            <b>Diretor: </b> {film.director} <br /> <br />
                            <b>Produtor: </b> {film.producer} <br /> <br />
                            <b>Data de Lanamento: </b> {film.release_date} <br /> <br />

                            <b>Sinopse: </b> Em Star Wars: Episdio I, quando a maquiavlica Federao Comercial
                            planeja invadir o pacfico planeta Naboo, o guerreiro Jedi Qui-Gon Jinn (Liam Neeson)
                            e seu aprendiz Obi-Wan Kenobi (Ewan McGregor) embarcam em uma aventura para tentar salvar
                            o planeta. Viajam com eles a jovem Rainha Amidala (Natalie Portman), que  visada pela
                            Federao pois querem for-la a assinar um tratado poltico. Eles tm de viajar para os
                            distantes planetas Tatooine e Coruscant em uma desesperada tentativa de salvar o mundo de
                            Darth Sidious (Ian McDiarmid), o demonaco lder da Federao que sempre surge em imagens
                            tridimensionais (a ameaa fantasma). Durante a viagem, Qui-Gon Jinn conhece um garoto de
                            nove anos que deseja trein-lo para ser tornar um Jedi, pois o menino tem todas as qualidades
                            para isto. Mas o tempo revelar que nem sempre as coisas so o que aparentam. <br /> <br />


                        </div>
                    </div>



                </Grid>
                <Grid item md={12} xs={12} sm={12}>
                    <Rodape />
                </Grid>

            </Grid>
        </>
    );
}

export default EpOne;
